import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
  tabBarElement: any;
  profileData;
  userId;
  userSession;
  loading = false;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public toastCtrl: ToastController,
              private carioApi: CarioApi) {
    this.tabBarElement = document.querySelector('.tabbar.show-tabbar');

    this.userId = localStorage.getItem('userId');
    this.userSession = localStorage.getItem('session');
  }

  ionViewDidLoad() {
    console.log(this.navParams.data);
    this.profileData = this.navParams.data;
  }

  ionViewWillEnter() {
    this.tabBarElement.style.display = 'none';
  }

  ionViewWillLeave() {
    this.tabBarElement.style.display = 'flex';
  }

  editAddress() {
    this.loading = true;
    let url = '?shop=1&ROUTE=ROUTE_PROFILE&ACTION=REQUEST_CHANGE_ADDRESS&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ADDRESS=NEW';
    this.carioApi.getApi(url).then( data => {
      console.log(data);
      let toast = this.toastCtrl.create({
        message: 'درخواست تغییر آدرس با موفقیت ارسال شد، همکاران ما در اسرع وقت با شما تماس خواهند گرفت',
        position: 'top',
        cssClass: 'success-toast',
        showCloseButton: true,
        closeButtonText: 'X'
      }); 
      toast.present();
      this.navCtrl.parent.select(0);
    }).catch((error: any) => {
      console.log('error: ',error);
      this.loading = false;
    });
  }
}



