import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';
import { CategoryPage } from '../category/category';

@IonicPage()
@Component({
  selector: 'page-category-list',
  templateUrl: 'category-list.html',
})
export class CategoryListPage {
	tabBarElement: any;
	cats;
	catId;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private carioApi: CarioApi,
				public viewCtrl: ViewController) {
    	this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
	}

	ionViewDidLoad() {
		this.catId = this.navParams.data.id;
		console.log(this.catId);
		this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_ALL_CATEGORIES').then( data => {
	        console.log(data);
			let c = [];
			c.push(data);
			//console.log('c::::',c[0]);
			for (var i = 0; i < c[0].length; i++) {
				let id = c[0][i].CategoryID; 
				if (id == this.catId) {
					let cat = c[0][i];
					this.cats = cat.SubCategories;
				}
			}
			//console.log('this val',this.cats);
		}).catch((error: any) => {
	        console.log('error: ',error);
	    });

	}
	
  	ionViewWillEnter() {
    	this.tabBarElement.style.display = 'none';
 	}
 
  	ionViewWillLeave() {
    	this.tabBarElement.style.display = 'flex';
  	}

 //  	closeModal() { 
	//     this.viewCtrl.dismiss();
	// }

	goToCatPage(c) {
		this.navCtrl.push(CategoryPage, {'id': c.CategoryID, 'name': c.CategoryName});
	}
}



