import { ToastController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { Http} from '@angular/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class CarioApi {

	private baseURL = 'http://app.cario.ir/index.php';

	constructor(private http: Http,
				public toastCtrl: ToastController) {

	}

//  post API
	postApi(params){
		let fullUrl = this.baseURL + params;
		console.log(fullUrl);
		return new Promise((resolve, reject) => {			
			this.http.post(fullUrl,'')
				.subscribe(res => {
					console.log(res);
					resolve(res.json());
				}, (err) => {
					let errorMessages = this.errorHandling(err);
			        reject(errorMessages);
			    }); 
		});
	}
//  get API
	getApi(apiUrl) {
		// this.setToken();
		return new Promise((resolve, reject) => {			
			this.http.get(this.baseURL + apiUrl)
				.subscribe(res => {
					resolve(res.json());
				}, (err) => {
					let errorMessages = this.errorHandling(err);
			        reject(errorMessages);
			    }); 
		});		
	}

//  checking user's phone number
	// postLogin(params) {
	// 	return this.postApi(this.baseURL,params);	
	// }

	// getAllCats(apiUrl) {
	// 	return this.getApi(this.baseURL + apiUrl);	
	// }


	errorHandling(err){
		if (err.status == 0) {
				let toast = this.toastCtrl.create({
					message: 'بروز مشکل از سمت سرور',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();

		} else {
			switch (err){
				case 'ACCOUNT_NOT_ACTIVATED':
					let toast = this.toastCtrl.create({
						message: 'این شماره قبلا در سیستم ثبت نشده',
						duration: 3000,
						position: 'top',
						cssClass: 'failed-toast',
						showCloseButton: true,
        				closeButtonText: 'X'
					}); 
					toast.present();

				case 0:
					return { 'status': err.status, 'msg': err.json().message };				
				case 404:
					return { 'status': err.status, 'msg': err.json().message };
				
				case 406:
					return { 'status': err.status, 'msg': err.json().message };
				
				case 403:
					return { 'status': err.status, 'msg': err.json().message };
				
				case 401:
					return { 'status': err.status, 'msg': err.json().message };
				case 405:
					return { 'status': err.status, 'msg': err.json().message };//'Method Not Allowed';
				case 500:
					return { 'status': err.status, 'msg': err.json().message };//'Server Error';

			}
		}
	}

}



