import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import * as moment from 'jalali-moment';

import { InAppBrowser, InAppBrowserEvent } from '@ionic-native/in-app-browser';

import { CarioApi } from '../../shared/shared';
import { BasketDataProvider } from '../../providers/basket-data/basket-data';
import { ProductDetailPage } from '../product-detail/product-detail';
import { DashboardPage } from '../dashboard/dashboard';

@IonicPage()
@Component({
  selector: 'page-basket',
  templateUrl: 'basket.html',
  providers: [BasketDataProvider]
})
export class BasketPage {
	tabBarElement: any;
	// basketCount;
	pMethod = 'online';
	unit;
	profit = 0;
	sum = 0;
  	fAmount = 0;
	// count = 0;
	//units;
	basketData;
  	chequeDate;
  	datePickerConfig = {
      	format: 'jYYYY/jM/jD',
      	disableKeypress: true,
      	allowMultiSelect: false,
      	min: moment(),
      	//isDayDisabledCallback: true,
  	};
  	//showItem:any;
  	chequeSelected = false;
  	userId;
  	userSession;
  	orderItemsArray:any = [];
  	orderItems = '';
  	shopInit;
  	fastDelivery = false;
  	fDelivery = 0;
  	loadingPage;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private carioApi: CarioApi,
				public basketPro: BasketDataProvider,
				private alertCtrl: AlertController,
				public storage: Storage,
				private loadingCtrl: LoadingController,
				public toastCtrl: ToastController,
				private iab: InAppBrowser) {
    	this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
	
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();

		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');

	    this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID='+this.userId+'&SESSION='+this.userSession).then( data => {
	        this.shopInit = data;
	        console.log('dataaaa',this.shopInit);
	        this.loadingPage.dismiss();
			this.datePickerConfig['max'] = moment().add(parseInt(this.shopInit.PaymentOptions.chequeDate),'day');
			console.log(this.datePickerConfig['max']);
		}).catch((error: any) => {
	        console.log('error: ',error);
	        this.loadingPage.dismiss();
	    });

	}

	ionViewDidLoad() {

	}

  	ionViewWillEnter() {
		// this.basketPro.favoriteFilm(newItem).then((data) => {
	 //      console.log('yessssss', data);
	 //    });
	    this.fAmount = 0;
		this.profit = 0;
	 	this.orderItemsArray = [];
	 	this.storage.get('cartItem').then((val) => {
	 		if (val) {
			    this.basketData = val;
			 	//console.log(this.basketData);
		    	for (var i = 0; i < this.basketData.length; i++) {
		    		this.fAmount += this.basketData[i].amount;
		    		this.profit += this.basketData[i].profit;
		    		this.orderItemsArray.push(this.basketData[i].id+'-'+this.basketData[i].count);
		    	}
		    	this.sum = this.fAmount + this.profit;
		    	//console.log('fff',this.orderItemsArray);
		    }
		});
    	this.tabBarElement.style.display = 'none';

 	}
 
  	ionViewWillLeave() {
    	this.tabBarElement.style.display = 'flex';
  	}

    close(d) {
        d.api.close();
    }

	getRadio(e) {
		console.log('method:',e);
		if (e == "cheque") {
			this.chequeSelected = true;
		} else {
			this.chequeSelected = false;
			//this.chequeDate = '0000-00-00';
		}
	}

	goToBankPage() {
		console.log(this.fAmount);
		let cDate;
		let finalAmount = this.fAmount;
		if (this.pMethod == "cheque") {
			if (this.chequeDate == undefined) {
				let toast = this.toastCtrl.create({
					message: 'درج تاریخ چک الزامی است',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
			} else {
				cDate = this.chequeDate.format('YYYY-MM-DD');
			}
		} else {
			cDate = '0000-00-00';
		}
		if (this.pMethod == "online") {
			finalAmount -= this.sum * 0.02;
			// console.log(finalAmount);
		}

		if (this.fastDelivery == true) {
			let fee = this.shopInit.FastDelivery.Fee;
			// console.log(fee , finalAmount);
			finalAmount += fee;
			// console.log(finalAmount);
			this.fDelivery = 1;
		}
		this.orderItems = this.orderItemsArray.join('_');
		console.log(this.orderItems);
		let url = '?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_REGISTER_ORDER&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ADDRESS=address&COMMENT=comments&AMOUNT='+this.sum+'&FINAL_AMOUNT='+finalAmount+'&ORDER_ITEMS='+this.orderItems+'&PAYMENT_METHOD='+this.pMethod+'&CHEQUE_DATE='+cDate+'&APP=3&ONLINE_FIRST='+this.shopInit.PaymentOptions.onlineFirst+'&FAST_DELIVERY='+this.fDelivery+'&FAST_DELIVERY_FEE='+this.shopInit.FastDelivery.Fee+'&FAST_DELIVERY_DATE=null';
		console.log(url);
		let loading = this.loadingCtrl.create({
			spinner: 'circles',
			content: '... در حال ارسال اطلاعات'
	    });
	    loading.present();

		this.carioApi.getApi(url).then(data => {
	        console.log(data);
	        if (data['STATE'] === 'SUCCESS') {
	        	console.log(finalAmount);
		        if (this.pMethod == 'online') {
		        	let orderId = data['ORDER_ID'];
					let url = 'http://app.cario.ir/index.php?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_PAYMENT&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ORDER_ID='+orderId;
					let browser = this.iab.create(url,'_system',{location:'yes'});
					this.basketPro.clearCart();
			        loading.dismiss();
			        setTimeout(() => {
			        	window.location.reload();
					}, 3000);
					browser.on('loadstart').subscribe((event:InAppBrowserEvent) =>{
						console.log('loadstart', event);
					    var closeUrl = 'cario://payment'; 
					    if(event.url == closeUrl){
					        browser.close();       //This will close InAppBrowser Automatically when closeUrl Started
					    }
					});
		        } else {
					let toast = this.toastCtrl.create({
						message: 'ارسال موفق اطلاعات',
						duration: 3000,
						position: 'top',
						cssClass: 'success-toast',
					    showCloseButton: true,
			        	closeButtonText: 'X'
					}); 
					toast.present();		        	
			        loading.dismiss();
			        this.basketPro.clearCart();
			        this.navCtrl.pop();
		        };
		    } else {
				let toast = this.toastCtrl.create({
					message: 'بروز خطا هنگام ارسال اطلاعات',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
				loading.dismiss();		    	
		    }
		}).catch((error: any) => {
	        console.log('error: ',error);
	        loading.dismiss();
	    });
	}

	goToProduct(id) {
		this.navCtrl.push(ProductDetailPage, {'id': id});
	}

	removeItem(item,index) {
		console.log(index);
	    let alert = this.alertCtrl.create({
	      message: 'آیا می خواهید این آیتم از سبد خرید شما حذف شود؟',
	      buttons: [
	        {
	          text: 'لغو',
	          role: 'cancel',
	          handler: () => {
	            console.log('Cancel clicked');
	          }
	        },
	        {
	          text: 'تایید',
	          handler: () => {
				this.basketPro.removeFromCart(index).then((data) => {
			      console.log('yessssss', data);
			      this.ionViewWillEnter();
			    });
	          }
	        }
	      ]
	    });
	    alert.present();
	}

}



