import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Searchbar, ModalController, LoadingController, ToastController } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import * as HighCharts from 'highcharts';
import * as moment from 'jalali-moment';
import 'rxjs/add/operator/debounceTime';

import { FactorDetailPage } from '../factor-detail/factor-detail';
import { CashOutPage } from '../cash-out/cash-out';
import { IncreaseCreditPage } from '../increase-credit/increase-credit';
import { ProductDetailPage } from '../product-detail/product-detail';
import { CategoryPage } from '../category/category';
import { BasketPage } from '../basket/basket';
import { CarioApi } from '../../shared/shared';

import { BasketDataProvider } from '../../providers/basket-data/basket-data';

@IonicPage()
@Component({
  selector: 'page-dashboard',
  templateUrl: 'dashboard.html',
  providers: [BasketDataProvider]
})

export class DashboardPage {
  todayDate;
  searchTerm: string = '';
  searchControl: FormControl;
  // dataItems: any;
  // showList= false;
  searchItems: any;
  showList;
  factorNo = 1111;
  loadingPage;
  initData;
  userName;
  purse;
  prize;
  userId;
  userSession;
  userType;
  codeBox;
  // codeBox1;
  // codeBox2;
  // codeBox3;
  // codeBox4;
  // codeBox5;
  mounth:any = [];
  priceSummary:any = [];
  profitSummary:any = [];
  invoices;
  basketCount;

  @ViewChild(Searchbar) searchbar: Searchbar;

  constructor(public navCtrl: NavController, 
  			  public navParams: NavParams,
  			  public modalCtrl: ModalController,
  			  private carioApi: CarioApi,
  			  private loadingCtrl: LoadingController,
  			  public toastCtrl: ToastController,
  			  private basketPro: BasketDataProvider,
  			  public storage: Storage,
  			  private barcodeScanner: BarcodeScanner) {
	this.loadingPage = this.loadingCtrl.create({
		spinner: 'circles',
    });
    this.loadingPage.present();

    //console.log(this.basketPro._cart);

    //this.basketCount = parseInt(localStorage.getItem('bCount'));
    this.storage.get('cartItem').then((val) => {
    	if (val) {
	    	this.basketCount = val.length;
	    }
	});
	this.userName = localStorage.getItem('userName');
	this.userId = localStorage.getItem('userId');
	this.userSession = localStorage.getItem('session');
	this.userType = localStorage.getItem('userType');

  	this.searchControl = new FormControl();
  }

  ionViewDidLoad() {
    //SHOP_INIT
    this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID='+this.userId+'&SESSION='+this.userSession).then( data => {
    //this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID=1&SESSION=c63c7861a2671659e3a5a123f10be97ec0c3ac8941e9741a15e2b8f7c351654b').then( data => {
        this.initData = data;
        console.log('dataaaa',this.initData);
  		this.purse = this.initData.PurseAndPrize.purse;
    	this.prize = this.initData.PurseAndPrize.ShoppingPrizeWithPurse;
	    
	    this.mounth = this.toArray(this.initData.Chart);

	    for (let key in this.initData.Chart) {
	    	if(this.initData.Chart[`${key}`][0].PriceSummary == null) {
	    		this.priceSummary.push(0);
	    	} else {
	    		this.priceSummary.push(this.initData.Chart[`${key}`][0].PriceSummary / 1000000);
	    	}	    	
	    }
	    
	    for (let key in this.initData.Chart) {
	    	if(this.initData.Chart[`${key}`][0].ProfitSummary == null) {
	    		this.profitSummary.push(0);
	    	} else {
	    		this.profitSummary.push(this.initData.Chart[`${key}`][0].ProfitSummary / 1000000);
	    	}	    	
	    }

		var myChart = HighCharts.chart('chartContainer', {
		  chart: {
		    type: 'spline'
		  },
		  title: {
		    text: ''
		  },
		  xAxis: {
		    categories: this.mounth
		  },
		  yAxis: {
		    title: {
		      text: 'میلیون تومان'
		    }
		  },
		  series: [{
		    name: 'سود ماهیانه',
		    data: this.profitSummary,
		    color: '#606ab7'
		  }, {
		    name: 'خرید ماهیانه',
		    data: this.priceSummary,
		    color: '#5faa35'
		  }],
		    tooltip: {
			    direction: 'ltr'
			}
		});

		this.invoices = this.initData.Invoices;
		//console.log(this.invoices);

		this.loadingPage.dismiss();

	}).catch((error: any) => {
        console.log('error: ',error);
        this.loadingPage.dismiss();
    });

	this.todayDate = moment().locale('fa').format('dddd DD MMMM YYYY');

    this.searchControl.valueChanges.debounceTime(250).subscribe(search => {

        if (this.searchTerm.length > 0) {
          this.showList= true;
        } else {
          this.showList= false;
        }        
        //this.setFilteredItems(); 
        this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_SUGGESTION&QUERY='+this.searchTerm).then(data => {
          this.searchItems = data;
          console.log(this.searchItems);
        });
    });
  }

  	ionViewWillEnter() {
        this.storage.get('cartItem').then((val) => {
        	//console.log(val);
        	if (val && val.length > 0) {
		    	this.basketCount = val.length;
        	} else {
		    	this.basketCount = 0;
        	}
		});
		// console.log(this.navParams.data); 
		// if(localStorage.getItem('pay') == 'true') {
		// 	console.log('pay',localStorage.getItem('pay'));
		// 	console.log('rrrrrrrrrrr');
		// 	let loading = this.loadingCtrl.create({
		// 		spinner: 'circles',
		//     });
		//     loading.present();
		// 	this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID='+this.userId+'&SESSION='+this.userSession).then( data => {
		//         this.invoices = data.Invoices;
		//        	loading.dismiss();
		//     	localStorage.removeItem('pay');
		//     	console.log('pay',localStorage.getItem('pay'));
		// 	}).catch((error: any) => {
		//         console.log('error: ',error);
		//        	loading.dismiss();
		//     });
		// }
 	}

  	toArray(value) {

	    if (!value) { return [] }

	    let arr = []; 

	    for(let key in value) {
		    switch (key) {
		    	case '1':
		    		{
		    			arr.push('فروردین');
		    			break;
		    		}
		    	case '2':
		    		{
		    			arr.push('اردیبهشت');
		    			break;
		    		}
		    	case '3':
		    		{
		    			arr.push('خرداد');
		    			break;
		    		}
		    	case '4':
		    		{
		    			arr.push('تیر');
		    			break;
		    		}
		    	case '5':
		    		{
		    			arr.push('مرداد');
		    			break;
		    		}
		    	case '6':
		    		{
		    			arr.push('شهریور');
		    			break;
		    		}
		    	case '7':
		    		{
		    			arr.push('مهر');
		    			break;
		    		}
		    	case '8':
		    		{
		    			arr.push('آبان');
		    			break;
		    		}
		    	case '9':
		    		{
		    			arr.push('آذر');
		    			break;
		    		}
		    	case '10':
		    		{
		    			arr.push('دی');
		    			break;
		    		}
		    	case '11':
		    		{
		    			arr.push('بهمن');
		    			break;
		    		}
		    	case '12':
		    		{
		    			arr.push('اسفند');
		    			break;
		    		}
		    }
	    }
	    //console.log(arr);
	    return arr;
	}

  goToBasket() {
  	this.navCtrl.push(BasketPage);
  }

  goToResult($event,item) {
    let typ = item.Type;

    switch(typ) { 
       case 'Product': { 
          this.navCtrl.push(ProductDetailPage, {id: item.id}); 
          break; 
       } 
       case 'Category': { 
          this.navCtrl.push(CategoryPage, item);
          break; 
       } 
       default: { 
          console.log('type of this item is undefined');
          break; 
       } 
    };
  }

  goToProduct() {
  	this.barcodeScanner.scan().then(barcodeData => {
	 	console.log('Barcode data', barcodeData);
	 	if (!barcodeData.cancelled) {
		  	let str= barcodeData.text;
		  	let pos = str.indexOf('=');
		  	let qrStr = str.substring(pos+1);
		  	//console.log(pos, '---' , qrStr);
			let loading= this.loadingCtrl.create({
				spinner: 'circles',
		    });
		    loading.present();
			this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_PRODUCT_BY_UNIQUE_ID&UNIQUE_ID='+qrStr).then( data => {
		    	console.log(data);
		    	this.navCtrl.push(ProductDetailPage, {id: data[0]['ProductID']}); 
				loading.dismiss();
			}).catch((error: any) => {
		        console.log('error: ',error);
		        loading.dismiss();
				let toast = this.toastCtrl.create({
					message: 'محصول موردنظر یافت نشد.',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
		    });
		}
	}).catch(err => {
	    console.log('Error', err);
		let toast = this.toastCtrl.create({
			message: 'بروز مشکل، مجددا تلاش کنید',
			duration: 3000,
			position: 'top',
			cssClass: 'failed-toast',
			showCloseButton: true,
        	closeButtonText: 'X'
		}); 
		toast.present();
	});
  }

  getCredit(codeId) {
  	let loading = this.loadingCtrl.create({
		spinner: 'circles',
    });
    loading.present();

    // let codeId = this.codeBox;//this.codeBox1 + this.codeBox2 + this.codeBox3 + this.codeBox4 + this.codeBox5;
    console.log(codeId); 
    this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_PRIZE_BY_UNIQUE_ID&UNIQUE_ID='+codeId+'&USER_ID='+this.userId+'&SESSION='+this.userSession).then( data => {
        console.log(data);
        loading.dismiss();
        if (data['error']) {
	        switch(data['error']) {
	        	case 'CODE WRONG OR ALREADY USED': {
					let toast = this.toastCtrl.create({
						message: 'کد وارد شده اشتباه است یا قبلا استفاده شده',
						duration: 3000,
						position: 'top',
						cssClass: 'failed-toast',
						showCloseButton: true,
		        		closeButtonText: 'X'
					}); 
					toast.present();
	        		break;
	        	}
	        	case 'FAILED': {
					let toast = this.toastCtrl.create({
						message: 'بروز مشکل از سمت سرور',
						duration: 3000,
						position: 'top',
						cssClass: 'failed-toast',
						showCloseButton: true,
		        		closeButtonText: 'X'
					}); 
					toast.present();
	        		break;
	        	}
	        }
	    } else {
	        this.prize = data['ProductPrizeForRepairMan'];
	        console.log(this.prize);
			let toast = this.toastCtrl.create({
				message: 'کد اعتبار شما با موفقیت افزوده شد',
				duration: 3000,
				position: 'top',
				cssClass: 'success-toast',
				showCloseButton: true,
        		closeButtonText: 'X'
			}); 
			toast.present();
			this.ionViewDidLoad();
			//loading.dismiss();
			//this.loadingPage.dismiss();
		}
	}).catch((error: any) => {
        console.log('error: ',error);
        loading.dismiss();
		// let toast = this.toastCtrl.create({
		// 	message: 'اتصال شما به اینترنت قطع می باشد',
		// 	duration: 3000,
		// 	position: 'top',
		// 	cssClass: 'failed-toast'
		// }); 
		// toast.present();
    });

  }

  getPrize() {
  	this.barcodeScanner.scan().then(barcodeData => {
	 	console.log('Barcode data', barcodeData);
	 	if (!barcodeData.cancelled) {
		  	let str= barcodeData.text;
		  	let pos = str.indexOf('=');
		  	let qrStr = str.substring(pos+1);
		  // 	let loading = this.loadingCtrl.create({
				// spinner: 'circles',
		  //   });
		  //   loading.present();

		    let codeId = qrStr;
		    //this.codeBox = codeId;
		    console.log(codeId); 
		    this.getCredit(codeId);
		}
	}).catch(err => {
	    console.log('Error', err);
		let toast = this.toastCtrl.create({
			message: 'بروز مشکل، مجددا تلاش کنید',
			duration: 3000,
			position: 'top',
			cssClass: 'failed-toast',
			showCloseButton: true,
        	closeButtonText: 'X'
		}); 
		toast.present();
	});  	
  }

  goToShopping() {
  	this.navCtrl.parent.select(1);//.push(ShoppingPage);
  }

  goToIncCredit() {
	let modal = this.modalCtrl.create(IncreaseCreditPage);
	modal.present();		
  }

  goToCashOut(purse) {
	let modal = this.modalCtrl.create(CashOutPage, {'purse':purse});
	modal.present();		
  }

  goToHistory() {
  	this.navCtrl.parent.select(2);
  }

  goToFactor(eId) {
  	this.navCtrl.push(FactorDetailPage,{'factorNo':eId});
  }

}



