import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';
import { ConfirmCodePage } from '../confirm-code/confirm-code';
import { RegisterPage } from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
	phoneNumber;
	phoneNumberValid = false;
	loading = false;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private carioApi: CarioApi,
				public toastCtrl: ToastController) {
	}

	ionViewDidLoad() {
	}

	checkLength(e) {
		if (e.length == 11) {
			this.phoneNumberValid = true;
		} else {
			this.phoneNumberValid = false;
		}
	}

	getCode() {
		this.loading = true;
		console.log('LoginPage');
		// let params = {
		// 	"ROUTE":  'LOGIN',
		// 	"ACTION": 'LOGIN',
		// 	"TEL":    this.phoneNumber,
		// 	"APP":    3
		// };
		let params = '?ROUTE=LOGIN&ACTION=LOGIN&TEL='+this.phoneNumber+'&APP=3';
		console.log(params);
		this.carioApi.postApi(params).then(data => {
			let msg = data['MESSAGE'];
			console.log(data['MESSAGE']);
			this.loading = false;
			if (msg == 'REGISTRATION_CODE') {
				let toast = this.toastCtrl.create({
					message: 'پیامک تایید برای شما ارسال شد',
					duration: 3000,
					position: 'top',
					cssClass: 'success-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'					
				});
				toast.present();
				this.navCtrl.push(ConfirmCodePage, this.phoneNumber);
			} else if(msg == 'ACCOUNT_NOT_ACTIVATED'){
				let toast = this.toastCtrl.create({
					message: 'این شماره در سیستم موجود نیست',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'
				}); 
				toast.present();
			} else {
				let toast = this.toastCtrl.create({
					message: 'شماره موبایل صحیح نمی باشد',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'
				}); 
				toast.present();	
			}
		}).catch((error: any) => {
			console.log('error: ',error);
			this.loading = false;
      	});
	}

	goRegister() {
		this.navCtrl.push(RegisterPage);
	}

}



