import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { FactorDetailPage } from '../factor-detail/factor-detail';
import { BasketPage } from '../basket/basket';
import { CarioApi } from '../../shared/shared';


@IonicPage()
@Component({
  selector: 'page-history',
  templateUrl: 'history.html',
})
export class HistoryPage {
	factorNo = 44444;
  	userName;
 	userId;
 	userSession;
 	loadingPage;
 	factors:any = [];
 	basketCount;

	constructor(public navCtrl: NavController,
				public navParams: NavParams,
				private carioApi: CarioApi,
				private loadingCtrl: LoadingController,
				public storage: Storage) {
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
    	//this.basketCount = parseInt(localStorage.getItem('bCount'));
	    this.storage.get('cartItem').then((val) => {
	    	if (val) {
		    	this.basketCount = val.length;
		    }
		});
		this.userName = localStorage.getItem('userName');
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
	}

	ionViewDidLoad() {
		// ?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_INVOICE&USER_ID=1&SESSION=c63c7861a2671659e3a5a123f10be97ec0c3ac8941e9741a15e2b8f7c351654b&START=0&LIMIT=5
    	this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_INVOICE&USER_ID='+this.userId+'&SESSION='+this.userSession+'&START=0&LIMIT=2000').then( data => {
	        console.log(data);
	        this.factors = data;
	        this.factors = this.factors.reverse();
	        this.loadingPage.dismiss();		
		}).catch((error: any) => {
	        console.log('error: ',error);
	        this.loadingPage.dismiss();
	    });
	}

  	ionViewWillEnter() {
        this.storage.get('cartItem').then((val) => {
    		if (val) {        	
		    	this.basketCount = val.length;
		    }
		});
 	}

  	goToBasket() {
  		this.navCtrl.push(BasketPage);
  	}

  	goToShopping() {
  		this.navCtrl.parent.select(1);
  	}

	goFactroDetail(fId, fPaied) {
		this.navCtrl.push(FactorDetailPage,{'factorNo':fId , 'factorType':fPaied});
	}

}



