import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Searchbar, LoadingController, ModalController } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { Storage } from '@ionic/storage';

import 'rxjs/add/operator/debounceTime';

import { ProductDetailPage } from '../product-detail/product-detail';
import { AddToBasketPage } from '../add-to-basket/add-to-basket';
import { CategoryPage } from '../category/category';
import { CategoryListPage } from '../category-list/category-list';
import { BasketPage } from '../basket/basket';
import { CarioApi } from '../../shared/shared';


@IonicPage()
@Component({
  selector: 'page-shopping',
  templateUrl: 'shopping.html',
})
export class ShoppingPage {
	cats;
	catsGroup;
	loadingPage;
	promise1; 
	promise2;
	searchControl: FormControl;
	searchTerm: string = '';
	searchItems: any;
	searchActive = false;
	showList;
	basketCount = 0;
	userType;

 	@ViewChild(Searchbar) searchbar: Searchbar;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				public modalCtrl: ModalController,
				private loadingCtrl: LoadingController,
				private carioApi: CarioApi,
				public storage: Storage) {
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
	    this.userType = localStorage.getItem('userType');

    	// this.basketCount = parseInt(localStorage.getItem('bCount'));
    	this.storage.get('cartItem').then((val) => {
    		if (val) {
		    	this.basketCount = val.length;
		    }
		});
	    this.searchControl = new FormControl();
	}

	ionViewDidLoad() {
		this.promise1 = this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_ROOT_CATEGORIES').then( data => {
			//let msg = data['MESSAGE'];
			//console.log('data: ',data);
			this.cats = data;
		}).catch((error: any) => {
			console.log('error: ',error);
      	});	

		this.promise2 = this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_MAIN_CATEGORIES&CATEGORY_ONLY=0').then( data => {
			//let msg = data['MESSAGE'];
			console.log('data: ',data);
			this.catsGroup = data;
		}).catch((error: any) => {
			console.log('error: ',error);
      	});	

      	Promise.all([this.promise1, this.promise2]).then(values => {
		  	console.log('values', values);
		  	this.loadingPage.dismiss();	
		});

	    this.searchControl.valueChanges.debounceTime(250).subscribe(search => {
	        //this.searchActive = true;

	        if (this.searchTerm.length > 0) {
	          this.showList= true;
	        } else {
	          this.showList= false;
	        }        
	        //this.setFilteredItems(); 
	        this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_SUGGESTION&QUERY='+this.searchTerm).then(data => {
	          this.searchItems = data;
	          console.log(this.searchItems);
	        });
	    });
	}
	
  	ionViewWillEnter() {
        this.storage.get('cartItem').then((val) => {
        	console.log(val);
        	if (val && val.length > 0) {
		    	this.basketCount = val.length;
        	} else {
		    	this.basketCount = 0;
        	}
		});
 	}

  	goToBasketPage() {
  		this.navCtrl.push(BasketPage);
  	}

  	checkFocus() {
    	this.searchActive = true;
  	}

  	cancelSearch($event) {
    	this.searchActive = false; 
  	}

	goItemDetail(id) {
		this.navCtrl.push(ProductDetailPage, {'id': id});
	}

	goToBasket(id) {
		let modal = this.modalCtrl.create(AddToBasketPage, {'id': id});
	    modal.onDidDismiss(() => {
			//this.basketCount = parseInt(localStorage.getItem('bCount'));
	        this.storage.get('cartItem').then((val) => {
	        	if (val) {
			    	this.basketCount = val.length;
			    }
			});
	    });
		modal.present();		
	}

	goToCatList(id) {
		// let modal = this.modalCtrl.create(CategoryListPage, {'id': id});
		// modal.present();		
		this.navCtrl.push(CategoryListPage, {'id': id});
	}

	goToResult($event,item) {
	    let typ = item.Type;

	    switch(typ) { 
	       case 'Product': { 
	          this.navCtrl.push(ProductDetailPage, {'id': item.id}); 
	          break; 
	       } 
	       case 'Category': { 
	          this.navCtrl.push(CategoryPage, item);
	          break; 
	       } 
	       default: { 
	          console.log('type of this item is undefined');
	          break; 
	       } 
	    };
	}

}



