import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { TabsPage } from '../tabs/tabs';
import { LoginPage } from '../login/login';
import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-confirm-code',
  templateUrl: 'confirm-code.html',
})
export class ConfirmCodePage {
	phoneNumber;
	code;
	codeValid = false;
	loading = false;

	constructor(public navCtrl: NavController,
				public navParams: NavParams,
				public storage: Storage,
				private carioApi: CarioApi,
				public toastCtrl: ToastController) {
	}

	ionViewDidLoad() {
		this.phoneNumber = this.navParams.data;
		console.log(this.phoneNumber);
	}

	checkLength(e) {
		if (e.length == 4) {
			this.codeValid = true;
		} else {
			this.codeValid = false;
		}
	}

	checkCode() {
		this.loading = true;
		let params = '?ROUTE=LOGIN&ACTION=ACTIVATION&TEL='+this.phoneNumber+'&APP=6&CODE='+this.code+'&REFERRER=-1';
		console.log(params);
		this.carioApi.postApi(params).then(data => {
			let msg = data['MESSAGE'];
			console.log(data);
			this.loading = false;
			if (msg == 'ACTIVATION_FAILED') {
				let toast = this.toastCtrl.create({
					message: 'کد وارد شده صحیح نمی باشد',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'					
				}); 
				toast.present();
			} else {
				let toast = this.toastCtrl.create({
					message: 'به کاریو خوش آمدید :)',
					duration: 3000,
					position: 'top',
					cssClass: 'success-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'
				});
				toast.present();

				this.navCtrl.push(TabsPage);
				localStorage.setItem('userId', data['USER_ID']);
				localStorage.setItem('session', data['SESSION']);
				localStorage.setItem('userName', data['USER_NAME']);
				localStorage.setItem('userEmail', data['USER_EMAIL']);
				localStorage.setItem('gender', data['USER_SEX']);
				localStorage.setItem('userRTtoken', data['USER_REFERRER_TOKEN']);
				localStorage.setItem('userRate', data['USER_RATING']);
				localStorage.setItem('userType', data['USER_TYPE']);
			}
		}).catch((error: any) => {
			console.log('error: ',error);
			this.loading = false;
      	});		
	}

	getCode() {
		this.navCtrl.push(LoginPage);
	}
}



