import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
import { HttpClientModule } from '@angular/common/http';

import { MyApp } from './app.component';
import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import { ConfirmCodePage } from '../pages/confirm-code/confirm-code';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { ShoppingPage } from '../pages/shopping/shopping';
import { HistoryPage } from '../pages/history/history';
import { FactorDetailPage } from '../pages/factor-detail/factor-detail';
import { ProductDetailPage } from '../pages/product-detail/product-detail';
import { AddToBasketPage } from '../pages/add-to-basket/add-to-basket';
import { CashOutPage } from '../pages/cash-out/cash-out';
import { IncreaseCreditPage } from '../pages/increase-credit/increase-credit';
import { CategoryPage } from '../pages/category/category';
import { CategoryListPage } from '../pages/category-list/category-list';
import { BasketPage } from '../pages/basket/basket';
import { FactorPaymentPage } from '../pages/factor-payment/factor-payment';
import { MorePage } from '../pages/more/more';
import { ProfilePage } from '../pages/profile/profile';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { DpDatePickerModule } from 'ng2-jalali-date-picker';
import { InAppBrowser } from '@ionic-native/in-app-browser'; 
import { Deeplinks } from '@ionic-native/deeplinks';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import { CarioApi } from '../shared/shared';
import { BasketDataProvider } from '../providers/basket-data/basket-data';


@NgModule({
  declarations: [
    MyApp,
    TabsPage,
    LoginPage,
    RegisterPage,
    ConfirmCodePage,
    DashboardPage,
    ShoppingPage,
    HistoryPage,
    FactorDetailPage,
    ProductDetailPage,
    AddToBasketPage,
    CashOutPage,
    IncreaseCreditPage,
    CategoryPage,
    CategoryListPage,
    BasketPage,
    ProfilePage,
    MorePage,
    FactorPaymentPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    DpDatePickerModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TabsPage,
    LoginPage,
    RegisterPage,
    ConfirmCodePage,
    DashboardPage,
    ShoppingPage,
    HistoryPage,
    FactorDetailPage,
    ProductDetailPage,
    AddToBasketPage,
    CashOutPage,
    IncreaseCreditPage,
    CategoryPage,
    CategoryListPage,
    BasketPage,
    ProfilePage,
    MorePage,
    FactorPaymentPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    CarioApi,
    InAppBrowser,
    Deeplinks,
    BasketDataProvider,
    BarcodeScanner
  ]
})
export class AppModule {}



