import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

const STORAGE_KEY = 'cartItem';

@Injectable()
export class BasketDataProvider {
	// public _cart:any = [];
	// public bObject = {
	// 	'id': 0,
	// 	'price': 0
	// }

	constructor(public http: HttpClient,
				public storage: Storage) {
		//console.log('Hello BasketDataProvider Provider');
		//console.log(this._cart);
	}

  addToCart(item) {
    return this.getAllItems().then(result => {
      if (result) {
        result.push(item);
        console.log(result);
        return this.storage.set(STORAGE_KEY, result);
      } else {
        return this.storage.set(STORAGE_KEY, [item]);
      }
    });
  }

  removeFromCart(index) {
    return this.getAllItems().then(result => {
      if (result) {
        //var index = result.indexOf(i);
        console.log(index);
        result.splice(index, 1);
        //result.splice(e, 1);
     //    if (i !== -1) {
	    //     result.splice(i, 1);
	    // }
        return this.storage.set(STORAGE_KEY, result);
      }
    });  	
  }

  getAllItems() {
    return this.storage.get(STORAGE_KEY);
  }

  getData() {
  	this.storage.get(STORAGE_KEY).then((val) => {
	    //console.log(val);
	    return val;
	});
  }

  	clearCart(){
		this.storage.set(STORAGE_KEY, []);
	}

	// hasItem(item) {
	// 	return (this._cart.indexOf(item) > -1);
	// }

	// addToCart(item) {
	// 	console.log('item', item);
	// 	return this._cart.push(item);
	// 	//console.log(this._cart);
	// }

	// removeFromCart(item) {
	// 	let index = this._cart.indexOf(item);
	// 	//item.checked=false;
	// 	if (index > -1) {
	// 	  this._cart.splice(index, 1);
	// 	}
	// }

	// clearCart(){
	// 	this._cart = [];
	// }

	// indexOfItem(item) {
	// 	return this._cart.indexOf(item);
	// }

	// countOfCart() {
	// 	return this._cart.length;
	// }
}



