import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-cash-out',
  templateUrl: 'cash-out.html',
})
export class CashOutPage {
	cash;
	purse;
	myPurse = 0;
	userId;
	userSession;
	selected = false;

  	constructor(public navCtrl: NavController, 
  			  	public navParams: NavParams,
  			  	public viewCtrl: ViewController,
  			  	public toastCtrl: ToastController,
  			  	private carioApi: CarioApi) {
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
  	}

	ionViewDidLoad() {
		this.purse = this.navParams.data.purse;
		console.log(this.purse);
	}

  	closeModal() { 
	    this.viewCtrl.dismiss();
	}

	getRadio(e) {
		console.log('purse:',e);
		this.selected = true;
	}

	checkData(e) {
		this.myPurse = e;
		this.cash = this.myPurse;
		this.selected = true;
	}

	cashOut() {
		console.log(this.cash);
		if (parseInt(this.cash) > parseInt(this.purse)) {
			let toast = this.toastCtrl.create({
				message: 'مبلغ انتخابی بیشتر از موجودی شماست',
				duration: 3000,
				position: 'top',
				cssClass: 'failed-toast',
				showCloseButton: true,
        		closeButtonText: 'X'
			}); 
			toast.present();

		} else {
		    this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=CASHOUT&USER_ID='+this.userId+'&SESSION='+this.userSession+'&AMOUNT='+this.cash).then( data => {
		        console.log(data);

				let toast = this.toastCtrl.create({
					message: 'درخواست شما با موفقیت ارسال شد',
					duration: 3000,
					position: 'top',
					cssClass: 'success-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'
				}); 
				toast.present();

			}).catch((error: any) => {
		        console.log('error: ',error);
				let toast = this.toastCtrl.create({
					message: 'بروز مشکل',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
	        		closeButtonText: 'X'
				}); 
				toast.present();
		    });
		}
	}

}



