import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

import { TabsPage } from '../tabs/tabs';
import { LoginPage } from '../login/login';
import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
	userMobile;
	userName;
	userPhone;
	userAddress;
	userDesc;
	userMail;
	loading = false;

	constructor(public navCtrl: NavController,
				public navParams: NavParams,
				private carioApi: CarioApi,
				public toastCtrl: ToastController) {
	}

	ionViewDidLoad() {
	}

	// checkLength(e) {
	// 	if (e.length == 11) {
	// 		this.phoneNumberValid = true;
	// 	} else {
	// 		this.phoneNumberValid = false;
	// 	}
	// }

	registerUser() {
		this.loading = true;
		let errorMsg = '';
		if (this.userMobile.length != 11) {
			errorMsg = 'شماره تلفن همراه به درستی وارد نشده';
			let toast = this.toastCtrl.create({
				message: errorMsg,
				duration: 3000,
				position: 'top',
				cssClass: 'failed-toast',
				showCloseButton: true,
        		closeButtonText: 'X'
			}); 
			toast.present();
			this.loading = false;
		} else {
			let params = '?ROUTE=LOGIN&ACTION=REQUEST_REGISTRATION&TEL='+this.userMobile+'&FIXED_TEL='+this.userPhone+'&APP=6&USER_NAME='+this.userName+'&ADDRESS='+this.userAddress+'&COMMENT='+this.userDesc;
			this.carioApi.postApi(params).then( data => {
				console.log(data);
				let toast = this.toastCtrl.create({
					message: 'اطلاعات شما با موفقیت ثبت شد، پس از بررسی با شما تماس گرفته می شود',
					duration: 3000,
					position: 'top',
					cssClass: 'success-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
				this.navCtrl.push(LoginPage);
				this.loading = false;
			}).catch((error: any) => {
				console.log('error: ',error);
				this.loading = false;
			});
		}	
		//this.navCtrl.push(TabsPage);
	}

}



