import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ModalController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';
import { FactorPaymentPage } from '../factor-payment/factor-payment';

@IonicPage()
@Component({
  selector: 'page-factor-detail',
  templateUrl: 'factor-detail.html',
})
export class FactorDetailPage {
	tabBarElement: any;
	factorNo;
	factorIsPaied;
	userId;
	userSession;
	userType;
	loadingPage;
	factorItems;
	prize;
	factors;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private carioApi: CarioApi,
				public modalCtrl: ModalController,
				private loadingCtrl: LoadingController) {
    	this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
		
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
		
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
		this.userType = localStorage.getItem('userType');
	}

	ionViewDidLoad() {
		this.factorNo = this.navParams.data.factorNo;
		this.factorIsPaied = this.navParams.data.factorType;
		// console.log('thissssss:',this.navParams.data);
		this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_INVOICE_BY_ID&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ORDER_ID='+this.factorNo).then( data => {
			this.factors = data[0];
			this.factorItems = data[0]['Items'];
			console.log(this.factors);
			console.log(this.factorItems);
			this.loadingPage.dismiss();
		}).catch((error: any) => {
	        console.log('error: ',error);
	        this.loadingPage.dismiss();
	    });
	}

  	ionViewWillEnter() {
    	this.tabBarElement.style.display = 'none';
 	}
 
  	ionViewWillLeave() {
    	this.tabBarElement.style.display = 'flex';
  	}

  	payFactor() {
  		let modal = this.modalCtrl.create(FactorPaymentPage, {'factor': this.factors});
	    modal.onDidDismiss(response => {
	        if (response == undefined) {
	        	this.navCtrl.popToRoot();
	        }
	    });
		modal.present();		

  	}

}



