import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, ModalController } from 'ionic-angular';

import { ProfilePage } from '../profile/profile';
import { LoginPage } from '../login/login';
import { CarioApi } from '../../shared/shared';
import { IncreaseCreditPage } from '../increase-credit/increase-credit';

@IonicPage()
@Component({
  selector: 'page-more',
  templateUrl: 'more.html',
})
export class MorePage {
	pages;
	loadingPage;
	userSession;
	userId;
	profileData;
  	tabBarElement: any;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				public alertCtrl: AlertController,
				private loadingCtrl: LoadingController,
				public modalCtrl: ModalController,
				private carioApi: CarioApi) {
	    this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
	
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
	}

	ionViewDidLoad() {		
		this.pages = [
			{ title: 'مشاهده اطلاعات کاربری', component: 'Profile', icon: 'contact'},
			{ title: 'خرید جدید', component: 'Shopping', icon: 'cart'},
			{ title: 'تاریخچه خرید', component: 'History', icon: 'archive'},
		]
    	
    	this.carioApi.getApi('?ROUTE=ROUTE_PROFILE&ACTION=PROFILE_GET_ALL&APP=1&SESSION='+this.userSession+'&USER_ID='+this.userId+'&VERSION=8').then( data => {
			this.loadingPage.dismiss();
			this.profileData = data;
			console.log(this.profileData);
		}).catch((error: any) => {
	        console.log('error: ',error);
	        this.loadingPage.dismiss();
	    });

	}

	goPage(p) {
		// if (p.component === 'ProfileInfoPage') {
		// 	this.navCtrl.push(p.component);
		// } else {
		// 	this.navCtrl.push(p.component,{'fromTab': true});
		// }
	    switch(p.component) { 
	       case 'Profile': { 
	          this.navCtrl.push(ProfilePage, this.profileData.ProfileContent); 
	          break; 
	       } 
	       case 'Shopping': { 
	          this.navCtrl.parent.select(1);
	          break; 
	       } 
	       case 'History': { 
	          this.navCtrl.parent.select(2);
	          break; 
	       } 
	       default: { 
	          console.log('type of this item is undefined');
	          break; 
	       } 
	    };
	}

	goToIncCredit() {
		let modal = this.modalCtrl.create(IncreaseCreditPage);
		modal.present();		
	}

	signOut() {
	    let alert = this.alertCtrl.create({
	      message: 'می خواهید از سیستم خارج شوید؟',
	      buttons: [
	        {
	          text: 'لغو',
	          role: 'cancel',
	          handler: () => {
	            console.log('Cancel clicked');
	          }
	        },
	        {
	          text: 'تایید',
	          handler: () => {
	            localStorage.clear();
	            localStorage.removeItem('userId');
  				localStorage.removeItem('session');
	            this.navCtrl.setRoot(LoginPage);
	            this.tabBarElement.style.display = 'none';
	            console.log(localStorage);
	          }
	        }
	      ]
	    });
	    alert.present();
	}

}



