import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController, LoadingController } from 'ionic-angular';
import * as moment from 'jalali-moment';
import { InAppBrowser, InAppBrowserEvent } from '@ionic-native/in-app-browser';

import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-factor-payment',
  templateUrl: 'factor-payment.html',
})
export class FactorPaymentPage {
	userId;
	userSession;
	factor;
	pMethod = 'online';
	chequeSelected = false;
	chequeDate;
	datePickerConfig = {
      	format: 'jYYYY/jM/jD',
      	disableKeypress: true,
      	allowMultiSelect: false,
      	min: moment()
  	};
  	basketData;
  	orderItemsArray:any = [];
  	orderItems = '';
  	shopInit;
  	fastDelivery = false;
  	fDelivery = 0;
  	loadingPage;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private loadingCtrl: LoadingController,
				public toastCtrl: ToastController,
				public viewCtrl: ViewController,
				private carioApi: CarioApi,
				private iab: InAppBrowser) {
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
		
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();

	    this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID='+this.userId+'&SESSION='+this.userSession).then( data => {
	        this.shopInit = data;
	        console.log('dataaaa',this.shopInit);
	        this.loadingPage.dismiss();
			this.datePickerConfig['max'] = moment().add(parseInt(this.shopInit.PaymentOptions.chequeDate),'day');
			console.log(this.datePickerConfig['max']);
		}).catch((error: any) => {
	        console.log('error: ',error);
	        this.loadingPage.dismiss();
	    });
	}

	ionViewDidLoad() {
		this.factor = this.navParams.data.factor;
		console.log(this.factor);
		this.basketData = this.factor.Items;
    	for (var i = 0; i < this.basketData.length; i++) {
    		this.orderItemsArray.push(this.basketData[i].ProductID+'-'+this.basketData[i].Count);
    	}
    	console.log(this.basketData);
	}

	closeModal() { 
	    this.viewCtrl.dismiss();
	}

    close(d) {
        d.api.close();
    }

	getRadio(e) {
		console.log('method:',e);
		if (e == "cheque") {
			this.chequeSelected = true;
		} else {
			this.chequeSelected = false;
			//this.chequeDate = '0000-00-00';
		}
	}

	goToBankPage() {
		console.log(this.chequeDate);
		let cDate;
		let finalAmount = this.factor.FinalPrice;
		if (this.pMethod == "cheque") {
			if (this.chequeDate == undefined) {
				let toast = this.toastCtrl.create({
					message: 'درج تاریخ چک الزامی است',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
			} else {
				cDate = this.chequeDate.format('YYYY-MM-DD');
			}
		} else {
			cDate = '0000-00-00';
		}
		if (this.pMethod == "online") {
			finalAmount -= finalAmount * 0.02;
			console.log(finalAmount);
		}

		if (this.fastDelivery == true) {
			let fee = this.shopInit.FastDelivery.Fee;
			// console.log(fee , finalAmount);
			finalAmount += fee;
			// console.log(finalAmount);
			this.fDelivery = 1;
		}
		this.orderItems = this.orderItemsArray.join('_');
		console.log(this.orderItems);
		let url = '?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_REGISTER_ORDER&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ADDRESS=address&COMMENT=comments&AMOUNT='+this.factor.FinalPrice+'&FINAL_AMOUNT='+finalAmount+'&ORDER_ITEMS='+this.orderItems+'&PAYMENT_METHOD='+this.pMethod+'&CHEQUE_DATE='+cDate+'&APP=3&ONLINE_FIRST='+this.shopInit.PaymentOptions.onlineFirst+'&FAST_DELIVERY='+this.fDelivery+'&FAST_DELIVERY_FEE='+this.shopInit.FastDelivery.Fee+'&FAST_DELIVERY_DATE=null';
		console.log(url);
		let loading = this.loadingCtrl.create({
			spinner: 'circles',
			content: '... در حال ارسال اطلاعات'
	    });
	    loading.present();

		this.carioApi.getApi(url).then(data => {
	        console.log(data);
	        if (data['STATE'] === 'SUCCESS') {
		        if (this.pMethod == 'online') {
		        	let orderId = data['ORDER_ID'];
					let url = 'http://app.cario.ir/index.php?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_PAYMENT&USER_ID='+this.userId+'&SESSION='+this.userSession+'&ORDER_ID='+this.factor.OrderID;
					let browser = this.iab.create(url,'_system',{location:'yes'});
			        loading.dismiss();
			        this.viewCtrl.dismiss(undefined);
			        setTimeout(() => {
			        	window.location.reload();
					}, 3000);
					browser.on('loadstart').subscribe((event:InAppBrowserEvent)=>{
					    var closeUrl = 'cario://payment'; 
					    if(event.url == closeUrl){
					        browser.close();       //This will close InAppBrowser Automatically when closeUrl Started
					    }
					});
		        } else {
					let toast = this.toastCtrl.create({
						message: 'ارسال موفق اطلاعات',
						duration: 3000,
						position: 'top',
						cssClass: 'success-toast',
						showCloseButton: true,
        				closeButtonText: 'X'
					}); 
					toast.present();
					loading.dismiss();
			        this.viewCtrl.dismiss(undefined);
		        };
		    } else {
				loading.dismiss();		    	
				let toast = this.toastCtrl.create({
					message: 'بروز خطا هنگام ارسال اطلاعات',
					duration: 3000,
					position: 'top',
					cssClass: 'failed-toast',
					showCloseButton: true,
        			closeButtonText: 'X'
				}); 
				toast.present();
		    }
		}).catch((error: any) => {
	        console.log('error: ',error);
	        loading.dismiss();
	    });

	}
}



