import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';

import { InAppBrowser, InAppBrowserEvent } from '@ionic-native/in-app-browser';

import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-increase-credit',
  templateUrl: 'increase-credit.html',
})
export class IncreaseCreditPage {
	userId;
	userSession;
	selected = false;
	cash;
	myPurse;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				public viewCtrl: ViewController,
				private carioApi: CarioApi,
				public toastCtrl: ToastController,
				private iab: InAppBrowser) {
		this.userId = localStorage.getItem('userId');
		this.userSession = localStorage.getItem('session');
	}

	ionViewDidLoad() {
		// console.log('ionViewDidLoad IncreaseCreditPage');
	}

	closeModal() { 
	    this.viewCtrl.dismiss();
	}

	getRadio(e) {
		console.log('purse:',e);
		this.selected = true;
	}

	checkData(e) {
		this.myPurse = e;
		this.cash = this.myPurse;
	}

	addCredit() {
		console.log(this.cash);
		let toast = this.toastCtrl.create({
			message: 'در حال انتقال به صفحه درگاه بانکی',
			duration: 3000,
			position: 'top',
			cssClass: 'success-toast',
			showCloseButton: true,
        	closeButtonText: 'X'
		}); 
		toast.present();

		let url = 'http://app.cario.ir/index.php?ROUTE=ROUTE_PURSE&ACTION=PURSE_ADD&APP=1&USER_ID='+this.userId+'&SESSION='+this.userSession+'&AMOUNT='+this.cash;
		let browser = this.iab.create(url,'_system',{location:'yes'});
		browser.on('loadstart').subscribe((event:InAppBrowserEvent)=>{
		    var closeUrl = 'cario://payment'; 
		    if(event.url == closeUrl){
		        browser.close();   
		    }
		});
        this.viewCtrl.dismiss();
        //console.log(url);

	 //    this.carioApi.getApi('').then( data => {
	 //        console.log(data);

		// 	let toast = this.toastCtrl.create({
		// 		message: 'درخواست شما با موفقیت ارسال شد',
		// 		duration: 3000,
		// 		position: 'top',
		// 		cssClass: 'success-toast'
		// 	}); 
		// 	toast.present();

		// }).catch((error: any) => {
	 //        console.log('error: ',error);
		// 	let toast = this.toastCtrl.create({
		// 		message: 'بروز مشکل',
		// 		duration: 3000,
		// 		position: 'top',
		// 		cssClass: 'failed-toast'
		// 	}); 
		// 	toast.present();
	 //    });

	}

}



