import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ModalController } from 'ionic-angular';

import { ProductDetailPage } from '../product-detail/product-detail';
import { AddToBasketPage } from '../add-to-basket/add-to-basket';
import { CarioApi } from '../../shared/shared';

@IonicPage()
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
})
export class CategoryPage {
	tabBarElement: any;
	catId; 
	catName;
	loadingPage;
	catItems;
	userType;

	constructor(public navCtrl: NavController, 
				public navParams: NavParams,
				private loadingCtrl: LoadingController,
				public modalCtrl: ModalController,
				private carioApi: CarioApi) {
    	this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
	    this.userType = localStorage.getItem('userType');
	}

	ionViewDidLoad() {
		this.catId = this.navParams.data.id;
		this.catName = this.navParams.data.name;
		console.log(this.navParams.data);
		this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_PRODUCT_BY_CATEGORY&CATEGORY_ID='+this.catId+'&START=0&LIMIT=1000').then(data => {
          	this.catItems = data;
          	console.log(this.catItems);
			this.loadingPage.dismiss();
        });
	}

  	ionViewWillEnter() {
    	this.tabBarElement.style.display = 'none';
 	}
 
  	ionViewWillLeave() {
    	this.tabBarElement.style.display = 'flex';
  	}

  	goItemDetail($event,item) {
  		this.navCtrl.push(ProductDetailPage, {id: item.ProductID});
  	}

  	goToBasket($event,item) {
		let modal = this.modalCtrl.create(AddToBasketPage, {id: item.ProductID});
		modal.present();
  	}

}



