import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, ToastController } from 'ionic-angular';

import { CarioApi } from '../../shared/shared';
import { BasketDataProvider } from '../../providers/basket-data/basket-data';

@IonicPage()
@Component({
  selector: 'page-add-to-basket',
  templateUrl: 'add-to-basket.html',
  providers: [BasketDataProvider]
})
export class AddToBasketPage {
	pId;
	product;
	images;
	unit;
	units;
	unitName;
	unitValue;
	count = 1;
	amount = 0;
	profit = 0;
	sellP;
	discount;
	loadingPage;
	basketCount;
	userType;

	constructor(public navCtrl: NavController,
				public navParams: NavParams,
				public viewCtrl: ViewController,
				private carioApi: CarioApi,
				private loadingCtrl: LoadingController,
				public basketPro: BasketDataProvider,
				public toastCtrl: ToastController) {
		this.loadingPage = this.loadingCtrl.create({
			spinner: 'circles',
	    });
	    this.loadingPage.present();
	    this.userType = localStorage.getItem('userType');
	}

	ionViewDidLoad() {
		this.pId = this.navParams.data['id'];
		console.log(this.pId);
		this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=GET_PRODUCT_BY_ID&PRODUCT_ID='+this.pId).then( data => {
			console.log(data);
			this.product = data[0];
			this.images = this.product['Images'];
			this.units = this.product['Units'];
			this.unitName = this.units[0].UnitName;
			this.unit = this.units[0].UnitCoefficient;
			this.amount = this.product['SellPrice'];
			this.sellP = this.amount;
			this.discount = this.product['Discount'];

			this.amount = this.count * this.unit * this.sellP;
			if(this.userType == 'repairman') {
				if (this.product.ProductPrizeForRepairMan >= 0 && this.product.ProductPrizeForRepairMan <= 100) {
					this.profit = this.amount * this.product.ProductPrizeForRepairMan / 100;
				} else {
					this.profit = this.product.ProductPrizeForRepairMan;
				}	
			} else {
				if (this.product.ProductPrizeForShop >= 0 && this.product.ProductPrizeForShop <= 100) {
					this.profit = this.amount * this.product.ProductPrizeForShop / 100;
				} else {
					this.profit = this.product.ProductPrizeForShop;
				}	
			}
	
			this.amount -= this.profit;

			this.loadingPage.dismiss();		 
		}).catch((error: any) => {
			console.log('error: ',error);
			this.loadingPage.dismiss();	
      	});
	}

	closeModal() { 
	    this.viewCtrl.dismiss();
	}

	getRadio(e,name) {
		console.log(e);
		//this.unitValue = e;
		this.unitName = name;
		console.log(this.unitName);
		this.amount = this.count * this.unit * this.sellP;
		if (this.discount >= 0 && this.discount <= 100) {
			this.profit = this.amount * this.discount / 100;
		} else {
			this.profit = this.amount - this.discount;
		}		 
		this.amount -= this.profit;
	}

	addNumber() {
		this.count ++;
		this.amount = this.count * this.unit * this.sellP;
		if (this.discount >= 0 && this.discount <= 100) {
			this.profit = this.amount * this.discount / 100;
		} else {
			this.profit = this.amount - this.discount;
		}		 
		this.amount -= this.profit;
	}

	minusNumber() {
		if (this.count > 0) {
			this.count --;
			this.amount = this.count * this.unit * this.sellP;
			if (this.discount >= 0 && this.discount <= 100) {
				this.profit = this.amount * this.discount / 100;
			} else {
				this.profit = this.amount - this.discount;
			}		 
			this.amount -= this.profit;
		}
	}

	addBasket() {
		//let newVal = parseInt(localStorage.getItem('bCount')) + 1;
		//this.basketCount = localStorage.setItem('bCount', `${newVal}`);
		//console.log(localStorage.getItem('bCount'));
		let newItem = {
			'id': this.product.ProductID,
			'name': this.product.ProductName,
			'unit': this.unit,
			'units': this.units,
			'amount': this.amount,
			'sellPrice': this.sellP,
			'discount': this.discount,
			'count': this.count * this.unit,
			'profit': this.profit
		}
		console.log(newItem);
		//this.basketPro.addToCart('newItem');
		this.basketPro.addToCart(newItem).then((data) => {
	      console.log('yessssss', data);
	    });

		let toast = this.toastCtrl.create({
			message: 'با موفقیت به سبد خرید افزوده شد',
			duration: 3000,
			position: 'top',
			cssClass: 'success-toast',
			showCloseButton: true,
        	closeButtonText: 'X'

		}); 
		toast.present();

		this.viewCtrl.dismiss();
	}

}



