import { Component } from '@angular/core';
import { Platform, App, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
 
import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import { ConfirmCodePage } from '../pages/confirm-code/confirm-code';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { ShoppingPage } from '../pages/shopping/shopping';
import { HistoryPage } from '../pages/history/history';
import { MorePage } from '../pages/more/more';

import { CarioApi } from '../shared/shared';
import { BasketDataProvider } from '../providers/basket-data/basket-data';

import { Deeplinks } from '@ionic-native/deeplinks';

@Component({
  templateUrl: 'app.html',
  providers: [BasketDataProvider]
})
export class MyApp {
  rootPage:any;
  isOldUser;
  basketCount;

  constructor(platform: Platform, 
              statusBar: StatusBar, 
              splashScreen: SplashScreen,
              public deeplinks: Deeplinks,
              private carioApi: CarioApi,
              public  app: App,
              public alertCtrl: AlertController) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      //statusBar.styleDefault();
      //statusBar.overlaysWebView(true);
      statusBar.backgroundColorByHexString('#5085bb');
      
      splashScreen.hide();
      // this.carioApi.getApi('?shop=1&ROUTE=ROUTE_SHOP&ACTION=SHOP_INIT&USER_ID=1&SESSION=c63c7861a2671659e3a5a123f10be97ec0c3ac8941e9741a15e2b8f7c351654b').then( data => {
      //   this.initData = data;
      //   console.log('dataaaa',this.initData);
      // }).catch((error: any) => {
      //   console.log('error: ',error);
      // });
      this.deeplinks.route({
        '/payment': TabsPage,
      }).subscribe((match) => {
        // match.$route - the route we matched, which is the matched entry from the arguments to route()
        // match.$args - the args passed in the link
        // match.$link - the full link data
        console.log('Successfully matched route', match);
      },
      (nomatch) => {
        // nomatch.$link - the full link data
        console.error('Got a deeplink that didn\'t match', nomatch);
      });

      this.isOldUser = localStorage.getItem('session');
      //this.basketCount = localStorage.setItem('bCount', '0');
      //console.log(this.isOldUser);
      if (this.isOldUser) {
        this.rootPage = TabsPage;
      } else {
        this.rootPage = LoginPage;
      }
    });

    platform.registerBackButtonAction(() => {
 
      let nav = app.getActiveNavs()[0];
      let activeView = nav.getActive();                
   
      // if(activeView.name === "DashboardPage" || activeView.name === "ShoppingPage" || activeView.name === "HistoryPage" || activeView.name === "MorePage") {
      //   const alert = this.alertCtrl.create({
      //       title: 'خروج از برنامه',
      //       message: 'آیا میخواهید از برنامه خارج شوید؟',
      //       buttons: [{
      //           text: 'لغو',
      //           role: 'cancel',
      //           handler: () => {
      //               console.log('Application exit prevented!');
      //           }
      //       },{
      //           text: 'تایید',
      //           handler: () => {
      //               platform.exitApp(); // Close this application
      //           }
      //       }]
      //   });
      //   alert.present();
      // } else {
        if(activeView.name === "DashboardPage" || activeView.name === "ShoppingPage" || activeView.name === "HistoryPage" || activeView.name === "MorePage") {
          const alert = this.alertCtrl.create({
              title: 'خروج از برنامه',
              message: 'آیا میخواهید از برنامه خارج شوید؟',
              buttons: [{
                  text: 'لغو',
                  role: 'cancel',
                  handler: () => {
                      console.log('Application exit prevented!');
                  }
              },{
                  text: 'تایید',
                  handler: () => {
                      platform.exitApp(); // Close this application
                  }
              }]
          });
          alert.present();
        }else if (nav.canGoBack()){ //Can we go back?
          nav.pop();
        } 
    });
  }
}



